<template>
  <div @click="changeImg" class="mask-img" :style="[maskUrl]"></div>
</template>

<script>
export default {
  name: 'Mheader',
  data() {
    return {
      max: 51,
      r: 0,
    }
  },
  methods: {
    changeImg() {
      this.r = Math.ceil(Math.random() * (this.max + 10))
    }
  },
  computed: {
    maskUrl() {
      let rImg = (() => {
        let img = Math.ceil(Math.random() * this.max).toString();
        return new Array(4 - img.length).join('0') + img;
      })();
      let rColor = this.r >= this.max ? (() => {
        let color = Math.floor(Math.random()*0xffffff).toString(16);
        return  new Array(7 - color.length).join('0') + color;
      })() : 'FFF';
      return {
        backgroundImage: this.r >= this.max ? 'url()' : 'url(' + 'http://aliyunoss.mldie.com/mldieblog/' + rImg + '.jpg' + ')',
        backgroundColor: '#' + rColor
      };
    }
  }
}
</script>

<style scoped>
.mask-img {
  cursor: pointer;
  max-width: 630px;
  height: 150px;
  /*background-color: #efefef;*/
  /*background-image: url(../../static/images/001.jpg);*/
  background-repeat:no-repeat;
  background-position:center; 
  background-size: 100% auto;
  mask-image: url(../../static/images/mldie.png);
  mask-repeat:no-repeat;
  mask-position:center; 
  mask-size: 100% auto;
}
</style>

